import { Component, OnInit, Input, Output, EventEmitter, SimpleChange } from '@angular/core';
import { Analyzes } from '../../analyzes';

@Component({
  selector: 'app-analyzes-product-list',
  templateUrl: './analyzes-product-list.component.html',
  styleUrls: ['./analyzes-product-list.component.css']
})
export class AnalyzesProductListComponent implements OnInit {
  @Input('analyzes') analyzes: Analyzes[] = [];

  @Output('check')
    eCheck = new EventEmitter<Analyzes>();

  public currentItem: Analyzes;
  public merge: Analyzes[] = [];  
  public mergeMode: boolean = false;
  public selectedItems: Analyzes[] = [];
  constructor() { }

  ngOnInit() {
    
  }

  clickItem(item): void {
    this.currentItem = item;
    
    this.defaultCheck(item);
  }

  selectItem(item): void {
    this.selectedItems.push(item);
  }

  unSelectItem(item) {
    this.selectedItems.forEach((found, index) => {
      if(found._id == item._id) this.selectedItems.splice(index, 1);
    });
  }

  isSelect(item: Analyzes) {
    return !!this.selectedItems.find((found) => {
      if(found._id === item._id) return true;
    });
  }

  getSelectedItems() {
    return this.selectedItems;
  }

  clearSelected() {
    this.selectedItems = []; 
  }

  private defaultCheck(item) {
    this.selectItem(item);
    this.eCheck.emit(item);
  }

}


