import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, Params } from '@angular/router';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';

import { ToolbarService } from '../../shared/toolbar.service';
import { ToastService } from '../../shared/toast.service';
import { PaginationService } from '../../shared/pagination.service';

import { AnalyzesService } from '../analyzes.service';

import { Analyzes, IAnalyzes } from '../analyzes';

@Component({
  selector: 'app-analyzes-update',
  templateUrl: './analyzes-update.component.html',
  styleUrls: ['./analyzes-update.component.css']
})
export class AnalyzesUpdateComponent implements OnInit {
  public analyz: Analyzes;
  public updateForm: FormGroup;

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _toolbar: ToolbarService,
    private _toastService: ToastService,
    private _pager: PaginationService,
    private _analyzesService: AnalyzesService) {
      
  }

  ngOnInit() {
    this._pager.notShow();

    this._toolbar.change({
      title: "Обновить анализ"
    });

    this.updateForm = new FormGroup({
      _id: new FormControl('', [<any>Validators.required]),
      art: new FormGroup({
				cmd: new FormControl(),
				helix: new FormControl(),
				invitro: new FormControl()
			}),
      title: new FormGroup({
				cmd: new FormControl(),
				helix: new FormControl(),
				invitro: new FormControl()
			}),
			priceLabs: new FormGroup({
				cmd: new FormControl(),
				helix: new FormControl(),
				invitro: new FormControl()
			}),
      priceProd: new FormGroup({
				cmd: new FormControl(),
				helix: new FormControl(),
				invitro: new FormControl()
			}),
			time: new FormGroup({
				cmd: new FormControl(),
				helix: new FormControl(),
				invitro: new FormControl()
			}),
      description: new FormControl(),
			active: new FormControl()
    });

    this._route.params.forEach((params: Params) => {
			let id = params['id']; 
			this._analyzesService.getOne(id)
				.subscribe(item => {
					this.analyz = new Analyzes(item);
					(<FormGroup>this.updateForm)
            			.patchValue(this.analyz);
				});
		});
  }

  clearForm(options?: any[]) : void {
    if(this.analyz != undefined) {
      this.analyz.clear();
    } else {
      this.analyz = new Analyzes();
    }
  }

   onSubmit(values, valid) {
    //console.log(values);
    if(valid) {
      this._analyzesService.update(values)
        .subscribe((data) => {
          this._router.navigate(['/analyzes']);
          this._toastService.toast("Анализ успешно обновлен!")
        });
    }
  }

}


