export interface IAnalyzes {
    _id: string;
    serialNumber: number;
    art: {
        helix: string,
        cmd: string,
        invitro: string
    };
    title: {
        helix: string,
        cmd: string,
        invitro: string
    };
    priceLabs: {
        helix: string,
        cmd: string,
        invitro: string
    };
    priceProd: {
        helix: number,
        cmd: number,
        invitro: number
    };
    time: {
        helix: string,
        cmd: string,
        invitro: string
    };
    price?: number;
    description: any;
    active: boolean;
}

export class Analyzes implements IAnalyzes {
    _id: string;
    serialNumber: number;
    art: any;
    title: any;
    priceLabs: any;
    priceProd: any;
    time: any;
    description: any;
    price?: number;
    active: boolean;

    get finishTitle() {
      return this.title.helix || this.title.cmd || this.title.invitro;
    }

    constructor(object? :any) {
        this.default();

        if(object != undefined) {
            this.setParams(object);
        }
    }

    setParams(object: any) : void {
        this._id = object._id || null;
        this.serialNumber = object.serialNumber || this.serialNumber;
        if(object.art) this.setLabs(this.art, object.art);
        if(object.title) this.setLabs(this.title, object.title);
        if(object.priceLabs) this.setLabs(this.priceLabs, object.priceLabs);
        if(object.priceProd) this.setLabs(this.priceProd, object.priceProd);
        if(object.time) this.setLabs(this.time, object.time);
        this.description = object.description || null;
        this.price = object.price || null;
        this.active = !!object.active;
    }

    clear() : void {
        this.default();
    }

    private setLabs(field, params) {
        field.helix = params.helix || null;
        field.cmd = params.cmd || null;
        field.invitro = params.invitro || null;
    }

    private default() : void {
        this._id = null;
		this.art = {
            helix: null,
            cmd: null,
            invitro: null   
        };
        this.title = {
            helix: null,
            cmd: null,
            invitro: null   
        };
        this.priceLabs = {
            helix: null,
            cmd: null,
            invitro: null   
        };
        this.priceProd = {
            helix: null,
            cmd: null,
            invitro: null 
        };
        this.time = {
            helix: null,
            cmd: null,
            invitro: null   
        };
        this.price = null;
        this.description = null;
        this.active = true;
    }
}



