import { Component, OnInit, Input, Output, EventEmitter, SimpleChange } from '@angular/core';
import { Analyzes } from '../../analyzes';

@Component({
  selector: 'app-analyzes-list',
  templateUrl: './analyzes-list.component.html',
  styleUrls: ['./analyzes-list.component.css']
})
export class AnalyzesListComponent implements OnInit {
  @Input('analyzes') analyzes: Analyzes[] = [];
  @Input('viewtype') viewType: string = 'full';
  @Input('options')
    set options(value: {
      tools?: {
        merge: boolean;
        edit: boolean;
        check: boolean;
        delete: boolean;
      };
      view?: {
        priceLabs: boolean;
        priceProd: boolean;
        price: boolean;
      };
    }) {
      if(value.tools !== undefined) {
        this.options.tools.edit = !!value.tools.edit;
        this.options.tools.check = !!value.tools.check;
        this.options.tools.delete = !!value.tools.delete;
        this.options.tools.merge = !!value.tools.merge;
      };

      if(value.view !== undefined) {
        this.options.view.priceLabs = !!value.view.priceLabs;
        this.options.view.priceProd = !!value.view.priceProd;
        this.options.view.price = !!value.view.price;
      };
    };
  
  get options() {
    return this._options;
  }

  @Output('startmerge')
    startMerge = new EventEmitter<void>();
  @Output('delete')
    eDelete = new EventEmitter<Analyzes>();
  @Output('check')
    eCheck = new EventEmitter<Analyzes>();
  @Output('edit')
    eEdit = new EventEmitter<Analyzes>();
    
  public _options = {
    tools: {
      merge: true,
      edit: true,
      delete: true,
      check: false
    },
    view: {
      priceLabs: true,
      priceProd: true,
      price: true
    }
  };

  public currentItem: Analyzes;
  public merge: Analyzes[] = [];  
  public mergeMode: boolean = false;
  public selectedItems: Analyzes[] = [];
  constructor() { }

  ngOnInit() {
    
  }

  deleteItem(item): void {
    this.currentItem = item;
    this.eDelete.emit(item);
  }

  mergeStart(item): void {
    this.mergeMode = true;
    
    this.selectedItems = [];
    this.startMerge.emit();
  }

  mergeStop(): void {
    this.mergeMode = false;
  }

  clickItem(item): void {
    this.currentItem = item;
    let deleted = false;
    if(this.mergeMode) {
      this.checkForMerge(item);
    } else {
      this.defaultCheck(item);
    }
  }

  editItem(item: Analyzes) {
    this.eEdit.emit(item);
  }

  selectItem(item): void {
    this.selectedItems.push(item);
  }

  unSelectItem(item) {
    this.selectedItems.forEach((found, index) => {
      if(found._id == item._id) this.selectedItems.splice(index, 1);
    });
  }

  isSelect(item: Analyzes) {
    return !!this.selectedItems.find((found) => {
      if(found._id === item._id) return true;
    });
  }

  getSelectedItems() {
    return this.selectedItems;
  }

  private checkForMerge(item): void {
    let domEl = <any>document.querySelector('#checkField'+item._id);
    
    if(this.isSelect(item)) {
      this.unSelectItem(item);
    } else {
      if(this.selectedItems.length < 3) {
        this.selectItem(item);
      } else {
        domEl.checked = false;
      }
    }
  }

  clearSelected() {
    this.selectedItems = []; 
  }

  private defaultCheck(item) {
    if(this.isSelect(item)) this.unSelectItem(item);
    else this.selectItem(item);
    this.eCheck.emit(item);
  }

}



