import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { SharedModule } from '../../shared/shared.module';

import { AnalyzesListComponent } from './analyzes-list/analyzes-list.component';
import { AnalyzesProductListComponent } from './analyzes-product-list/analyzes-product-list.component';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    SharedModule
  ],
  declarations: [AnalyzesListComponent, AnalyzesProductListComponent],
  exports: [AnalyzesListComponent, AnalyzesProductListComponent]
})
export class AnalyzesWidgetModule { }



