import { Component, OnInit, EventEmitter, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { Analyzes } from '../analyzes';
import { AnalyzesService } from '../analyzes.service';

import { ToolsService } from '../../shared/tools.service';
import { PaginationService } from '../../shared/pagination.service';

import { ModalComponent } from '../../shared/modal/modal.component';
import { AnalyzesListComponent } from '../_widgets/analyzes-list/analyzes-list.component';


@Component({
  selector: 'app-analyzes',
  templateUrl: './analyzes.component.html',
  styleUrls: ['./analyzes.component.css']
})
export class AnalyzesComponent implements OnInit {
  @ViewChild('mergeModal') mergeModal: ModalComponent;
  @ViewChild('analyzesList') analyzesList: AnalyzesListComponent;

  public analyzes$: Observable<Analyzes[]>;
  public filter: any;

  constructor(
    private _analyzesService: AnalyzesService,
    private _tools: ToolsService,
    private _router: Router,
    private _pagination: PaginationService
  ) {
    this.initAnalyzes();
  }

  initAnalyzes() {
    this._pagination.setParams({
      nPerPage: 10
    }, 'analyzes-list');
    this.reloadAnalyzes();
  }

  ngOnInit() {
    this.filter = {
      labs: {helix: false, invitro: false, cmd: false}
    };

    this._tools.getToolbar().change({
      title: "Список анализов",
      name: "analyzes",
      actions: {
        //add: () => this._router.navigate(['analyzes/add']),
        search: (searchText) => {
          this._pagination.setParams({
              search: searchText,
              currentPage: 1
            }, 'analyzes-list');
          this.reloadAnalyzes();
        },
        done: {
          ok: () => {
            this.mergeModal.open();
          },
          close: () => {
            this.analyzesList.mergeStop();
          }
        }
      },
      actionsPosition: {
        extend: ['search'] //'add', 
      }
    });
      
    this.reloadAnalyzes();
  }

  deleteItem(item: Analyzes) {
    let self = this;
    const deleteAnalyzes$ = this._analyzesService.remove(item._id);
    
    deleteAnalyzes$
      .subscribe(
        data => { 
          if(data.status == 'ok') {
            this.reloadAnalyzes();
            this._tools.getToast().toast("Анализ удален");
          }
        }
      );
  }

  editItem(item: Analyzes) {
    this._router.navigate(['/analyzes/update/'+item._id]);
  }

  mergeItems(items: Analyzes[]): void {
    const mergeAnalyzes$ = this._analyzesService.merge(items);
    
    mergeAnalyzes$
      .subscribe((data) => {
        this.analyzesList.mergeStop();
        this._tools.getToolbar().setMode('default');
        this.reloadAnalyzes();
      });
  }

  changePageAnalyzes(params) {
    this.reloadAnalyzes(params);
  }

  reloadAnalyzes(params?: any) {
    params = params !== undefined ? params : this._pagination.getParams('analyzes-list');

    this.analyzes$ = this._analyzesService.get(params)
      .map((analyzes) => analyzes.map(analyz => new Analyzes(analyz)));
    const analyzesCount$ = this._analyzesService.getCount(params);

    const $combined = Observable.concat(
      this.analyzes$,
      analyzesCount$
    );

    $combined
      .subscribe(
        result => {
          if(typeof result === 'number') {
            this._pagination.setParams({
              countItems: result
            }, 'analyzes-list');
            this._pagination.getPager('analyzes-list');
          }
        }
      );
  }
}



