import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions, URLSearchParams } from '@angular/http';
import 'rxjs/Rx';
import { Observable } from 'rxjs/Rx';

import { HttpClient } from '../+users/_services/http-client.service';

import { IAnalyzes, Analyzes } from './analyzes';

@Injectable()
export class AnalyzesService {
  	private _baseUrl: string = "/api/v1/analyzes";

  	constructor(private _http: HttpClient) { }

  	get(params) : Observable<Analyzes[]> {
		let query = new URLSearchParams();
		query.set('nPerPage', params.nPerPage);
		query.set('pageNumber', params.currentPage);
		if(params.search) query.set('search', params.search);
		if(params.labs) {
			let labs = [];
			Object.keys(params.labs).map((lab) => {
				if(params.labs[lab]) labs.push(lab);
			});
			
			if(labs.length > 0) query.set('labs', labs.toString());
		}

		return this._http.get(this._baseUrl, {search: query})
			.map( (res: Response) => res.json() )
			.catch( (error: any) => Observable.throw(error.json().error || "Server error"))
			.share();
	}

	getCount(params) : Observable<number> {
		let query = new URLSearchParams();
		query.set('nPerPage', params.nPerPage);
		query.set('pageNumber', params.currentPage);
		if(params.search) query.set('search', params.search);
		if(params.labs) {
			let labs = [];
			Object.keys(params.labs).map((lab) => {
				if(params.labs[lab]) labs.push(lab);
			});
			
			if(labs.length > 0) query.set('labs', labs.toString());
		}

		return this._http.get(`${this._baseUrl}/count`, {search: query})
			.map( (res: Response) => res.json() )
			.catch( (error: any) => Observable.throw(error.json().error || "Server error"))
			.share();
	}

	getOne(id: string) : Observable<Analyzes> {
		return this._http.get(`${this._baseUrl}/${id}`)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().error || 'Server error'));
	}

	add(body: IAnalyzes) : Observable<Analyzes> {
		let bodyString = JSON.stringify(body);
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});

		return this._http.post(this._baseUrl, JSON.stringify(body), options)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().error || 'Server error'));
	}

	update(body: IAnalyzes) : Observable<Analyzes> {
		let bodyString = JSON.stringify(body);
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});

		return this._http.put(`${this._baseUrl}/${body._id}`, JSON.stringify(body), options)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().error || 'Server error'));
		}
	
	merge(items: IAnalyzes[]) : Observable<Analyzes> {
		let ids = { ids: items.map((item) => item._id) };
		let bodyString = JSON.stringify(items);
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});

		return this._http.post(`${this._baseUrl}/merge`, JSON.stringify(ids), options)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().error || 'Server error'));
	}

	remove(id: string) : Observable<any> {
		let headers = new Headers({'Content-type': 'application/json'});
		let options = new RequestOptions({headers: headers});
		
		return this._http.delete(`${this._baseUrl}/${id}`)
			.map((res: Response) => res.json())
			.catch((error: any) => Observable.throw(error.json().error || 'Server error'));
		}
}



