import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { SharedModule } from '../shared/shared.module';

import { AnalyzesWidgetModule } from './_widgets/analyzes.widget';
import { UsersWidgetModule } from '../+users/_widgets/users.widget';

import { AnalyzesComponent } from './analyzes/analyzes.component';
import { AnalyzesAddComponent } from './analyzes-add/analyzes-add.component';
import { AnalyzesUpdateComponent } from './analyzes-update/analyzes-update.component';

import { AnalyzesService } from './analyzes.service';

const ANALYZES_ROUTES: Routes = [
  { path: '', component: AnalyzesComponent },
  { path: 'add', component: AnalyzesAddComponent },
  { path: 'update/:id', component: AnalyzesUpdateComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(ANALYZES_ROUTES),
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    SharedModule,
    AnalyzesWidgetModule,
    UsersWidgetModule
  ],
  declarations: [AnalyzesComponent, AnalyzesAddComponent, AnalyzesUpdateComponent],
  providers: [AnalyzesService],
  exports: [AnalyzesComponent]
})
export class AnalyzesModule { }



